.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #3e3b5c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#button1 {
  margin-left: 30%;
  margin-top: 10%;
}
.nav_title {
  color: rgb(47, 255, 82);
  font-weight: bold;
}
.heading_textbox {
  background-color: rgb(27, 132, 50);
  padding-top: 10px;
  text-align: center;
}
